.posts__wrap {
    width: 676px;
    margin: 50px auto;
}

.posts__item {
    margin-bottom: 20px;

    border: solid 10px var(--third-bg-color);
    box-shadow: 5px 5px 10px 0px rgba(0,0,0,0.75);

    background-color: var(--bg-color1);

    transition: var(--def-trs);
}

.posts {
    height: auto;
}

.posts__title {
    padding-bottom: 5px;

    font-weight: bold;
}

.posts__text {
    margin: 0;
    padding-bottom: 5px;

    font-weight: normal;
}

.posts__capt {
    opacity: .5;

    font-weight: normal;
}

.posts__img {
    width: 100%;
}

.posts__link {
    display: block;

    padding: 20px;

    color: var(--text-color1);
    text-decoration: none;
}

.posts__link:active {
    color: var(--bg-color2);
}

.posts__mob {
    display: none;

    opacity: .6;
    font-weight: normal;
}

@media (hover: hover) {
    .posts__wrap:hover .posts__item{
        filter: blur(.7px);
    }

    .posts__wrap:hover .posts__item:hover{
        filter: drop-shadow( 0px 0px 8px rgba(0, 0, 0, 1)) blur(0);
    }
}

@media screen and (max-width: 1024px) {
    .posts {
        height: auto;
    }

    .posts__wrap {
        padding: var(--pad-txt);
        margin: 30px auto;
    }

    .posts__back {
        position: absolute;
    }
}

@media screen and (max-width: 735px) {
    .posts__wrap {
        padding: 10px;
        margin: 0 0 40px;
    }

    .posts__mob {
        display: block;
    }

    .posts__capt {
        display: none;
    }
}
